import React, { Component } from 'react';
import {LocaleProvider} from 'antd';
import { BrowserRouter as Router,Route} from 'react-router-dom';
import createHistory from 'history/createBrowserHistory';
//翻译相关
import intl from 'react-intl-universal';
import zhCN from 'antd/lib/locale-provider/zh_CN';
import enUS from 'antd/lib/locale-provider/en_US';
//引入页面
import Home from './pages/home/home';

const history = createHistory();
const supportsHistory = 'pushState' in window.history;
const locales = {
    "en-US": require('./i18n/en.json'),
    "zh-CN": require('./i18n/zh.json'),
};
const antdLocales = {
    "en-US": enUS,
    "zh-CN": zhCN
};

class App extends Component {
    state = {
        initDone: false,
        currentLocale:'zh-CN'
    };
    componentDidMount() {
        this.loadLocales();
    }
    componentDidUpdate(prevProps, prevState){
        if(prevState.currentLocale !== this.state.currentLocale){
            this.loadLocales();
        }
    }
    loadLocales() {
        intl.init({
            currentLocale: this.state.currentLocale, // TODO: determine locale here
            locales,
        }).then(() => {
            this.setState({initDone: true});
        });
    }
    changeLocale = ()=>{
        this.setState((prevState)=>{
                return{
                    currentLocale:prevState.currentLocale === 'zh-CN'?'en-US':'zh-CN'
                }
            }
        )
    };
  render() {
        const {currentLocale} = this.state;
        return (
            <LocaleProvider locale={antdLocales[currentLocale]}>
                <Router  forceRefresh={!supportsHistory} >
                    <div style={{height:'100%'}}>
                        <Route exact path="/"  render={(history)=><Home history={history} changeLocale={this.changeLocale} />} />
                    </div>
                </Router>
            </LocaleProvider>
        );
  }
}

export default App;



